<template>
  <div>
    <el-form style="height: 45px" :inline="true">
      <el-form-item style="height: 45px">
        <el-button style="margin: 5px" type="primary" @click="dialogVisible = true;getItemList"></el-button>
      </el-form-item>
      <el-form-item style="height: 45px">
        <el-popconfirm
            title=""
            @confirm="handleDelete(null)"
        >
          <el-button
              type="danger"
              slot="reference"
              style="margin: 5px" :disabled="canDelete"></el-button>
        </el-popconfirm>
      </el-form-item>
    </el-form>
    <el-table
        :data="tableData"
        ref="multipleTable"
        :row-key="(row) => {return row.spId}"
        @selection-change="handleSelectionChange"
    >
      <el-table-column
          type="selection"
          :reserve-selection="true"
          align="center"
          width="55">
      </el-table-column>
      <el-table-column
          label=""
          align="center"
          prop="spId">
      </el-table-column>
      <el-table-column
          label=""
          align="center"
          prop="sprId">
      </el-table-column>
      <el-table-column
          label=""
          align="center"
          prop="sprName">
      </el-table-column>
      <el-table-column
          label=""
          align="center"
          prop="gdId">
      </el-table-column>
      <el-table-column
          label=""
          align="center"
          prop="gdName">
      </el-table-column>
      <el-table-column
          label=""
          align="center"
          prop="spAmount">
      </el-table-column>
      <el-table-column
          label=""
          align="center"
          prop="spPrice">
      </el-table-column>
      <el-table-column
          align="center"
          label="">
        <template slot-scope="scope">
          <el-button
              size="mini"
              type="text"
              @click="handleEdit(scope.row.spId);getItemList"></el-button>
          <el-divider direction="vertical"></el-divider>
          <el-popconfirm
              title=""
              @confirm="handleDelete(scope.row.spId)">
            <el-button
                size="mini"
                type="text"
                slot="reference"
            ></el-button>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
        style="height: 32px"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        layout="total, sizes, prev, pager, next, jumper"
        :current-page="current"
        :page-sizes="[1, 10, 20, 50, 100]"
        :page-size="size"
        :total="total"/>
    <el-dialog
        title=""
        :visible.sync="dialogVisible"
        width="30%"
        :before-close="handleClose">
      <el-form :model="editForm" :rules="rules" ref="editForm" label-width="100px" class="demo-editForm">
        <el-form-item label="" prop="sprId">
          <el-select v-model="editForm.sprId" placeholder="">
            <el-option :label="item.sprName" :value="item.sprId" v-for="(item, index) in sprItemList" :key="index"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="sprId">
          <el-select v-model="editForm.sprId" placeholder="">
            <el-option :label="item.gdName" :value="item.gdId" v-for="(item, index) in gdItemList" :key="index"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="spAmount">
          <el-input type="number" min="0" v-model="editForm.spAmount"></el-input>
        </el-form-item>
        <el-form-item label="" prop="spPrice">
          <el-input type="number" min="0" step="0.5" v-model="editForm.spPrice"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('editForm')"></el-button>
          <el-button @click="resetForm('editForm')"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import axios from "../../axios";
import store from "../../store";

export default {
  name: "Supply",
  data() {
    return {
      total: 0,
      size: 10,
      current: 1,

      canDelete: true,

      itemList: [ ],
      dialogVisible: false,
      editForm: {
        spId: null,
        sprId: null,
        sprName: '',
        gdId: null,
        gdName: '',
        spAmount: '',
        spPrice: ''
      },
      rules: {
        sprId: [
          { required: true, message: '', trigger: 'change' }
        ],
        gdId: [
          { required: true, message: '', trigger: 'change' }
        ],
        spAmount: [
          { required: true, message: '', trigger: 'blur' }
        ],
        spPrice: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      tableData: [],
      multipleSelection: [],
      search: ''
    }
  },
  created() {
    this.getSupplies()
  },
  methods: {
    handleEdit(spId) {
      this.$axios.get('/sys/supply/info/' + spId).then(res => {
        this.editForm = res.data.data
        this.dialogVisible = true
      })
    },
    handleDelete(spId) {
      let spIds = [ ]
      if (spId) {
        spIds.push(spId)
      } else {
        this.multipleSelection.forEach(row => {
          spIds.push(row.spId)
        })
      }
      this.$axios.post('/sys/goodsType/delete/', spIds).then(res => {
        this.$message({
          showClose: true,
          message: 'Succeed',
          type: 'success',
          onClose: () => {
            this.getSupplies()
          }
        })
      })
    },
    handleClose() {
      this.resetForm('editForm')
      this.dialogVisible = false
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$axios.post('/sys/supply/' + (this.editForm.spId ? 'update' : 'save'), this.editForm)
              .then(res => {
                this.$message({
                  showClose: true,
                  message: 'Succeed',
                  type: 'success',
                  onClose: () => {
                    this.getSupplies()
                  }
                })
                this.resetForm('editForm')
              })
        } else {
          this.$message.error('Failed')
        }
      })
    },
    resetForm(formName) {
      if (this.$refs[formName] !== undefined) {
        this.$refs[formName].resetFields()
      }
      this.editForm = {
        spId: null,
        sprId: null,
        sprName: '',
        gdId: null,
        gdName: '',
        spAmount: '',
        spPrice: ''
      }
    },
    getSupplies() {
      this.$axios.get('/sys/supply/list', {
        params: {
          current: this.current,
          size: this.size
        }
      }).then(res => {
        this.tableData = res.data.data.records
        this.size = res.data.data.size
        this.current = res.data.data.current
        this.total = res.data.data.total
      })
    },
    getItemList() {
      this.$axios.get('/sys/supply/selectInfo').then(res => {
        this.sprItemList = res.data.data.sprItemList
        this.gdItemList = res.data.data.gdItemList
      })
    },
    handleSelectionChange(val) {
      this.multipleSelection = val
      this.canDelete = val.length == 0
    },
    handleSizeChange(val) {
      console.log(` ${val} `);
      this.size = val
      this.getSupplies()
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`);
      this.current = val
      this.getSupplies()
    }
  },
}
</script>

<style scoped>
.el-pagination {
  float: right;
  margin-top: 22px;
}
</style>

<style>
.el-dialog__title {
  position:absolute;
}

.el-dialog__header {
  position:relative;
  height: 50px;
}
</style>